body {
  width: 100%;
  height: 100%;
    font-family: 'Indie Flower', cursive;
}

h1 {
  font-size: 50px;
  text-align: center;
}

h3 {
  font-size: 30px;
  text-align: center;
}

.board {
  position: absolute;
  top: 50%;
  right: 50%;
  margin: -300px -300px 0 0;
  display: -ms-flexbox;
  display: flex;
  width: 600px;
  height: 600px;
  -ms-flex-direction: row;
      flex-direction: row;
  -ms-flex-wrap: wrap;
      flex-wrap: wrap;
  -ms-flex-pack: start;
      justify-content: flex-start;
}

.square {
  width: 200px;
  height: 200px;
  -webkit-box-sizing: border-box;
          box-sizing: border-box;
  border: 5px solid black;
  text-align: center;
  vertical-align: middle;
  font-size: 110px;
  font-weight: 800;
  padding: 35px;
  font-family: 'Indie Flower', cursive;
}

.square:hover {
  background-color: #0dbae8;
  cursor: pointer;
}

.square:nth-child(1){
  border-radius: 25px 0 0 0;
}

.square:nth-child(3){
  border-radius: 0 25px 0 0;
}

.square:nth-child(7){
  border-radius: 0 0 0 25px;
}

.square:nth-child(9){
  border-radius: 0 0 25px 0;
}

.endGame {
  display: none;
  position: absolute;
  top: 50%;
  right: 50%;
  margin: -300px -300px 0 0;
  width: 600px;
  height: 600px;
  border: 5px solid #0dbae8;
  border-radius: 25px;
  -webkit-box-sizing: border-box;
          box-sizing: border-box;
}

.endGame h1 {

  font-size: 65px;
}

.newGame {
  display: inline-block;
    font-weight: 400;
    text-align: center;
    white-space: nowrap;
    vertical-align: middle;
    -webkit-user-select: none;
    -moz-user-select: none;
    -ms-user-select: none;
    user-select: none;
    border: 1px solid transparent;
    padding: .5rem .75rem;
    font-size: 1rem;
    line-height: 1.25;
    border-radius: .25rem;
    -webkit-transition: all .15s ease-in-out;
    -o-transition: all .15s ease-in-out;
    transition: all .15s ease-in-out;
    background-color: #0dbae8;
    color: #fff;
    cursor: pointer;
    width: 300px;
    height: 100px;
}

.newGame:hover {
  background-color: #0176ab;
}

.button-container {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-align: center;
      align-items: center;
  -ms-flex-pack: center;
      justify-content: center;
  margin: 25px 0;
}


